@font-face {
  font-family: 'Kumbh Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('KumbhSans-Regular.woff') format('woff'),
    url('KumbhSans-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'Kumbh Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url('KumbhSans-Bold.woff') format('woff'),
    url('KumbhSans-Bold.woff2') format('woff2');
}
